import React from 'react'
import {Layout, Menu, Dropdown, Icon, Spin} from 'antd'
import { Link } from 'react-router-dom'
import BookItem from './bookItem'
import styles from '../styles/main.less'
import template from './template'
import ReactPullToRefresh from 'react-pull-to-refresh'


const { Header, Content } = Layout

class AppComponent extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      bookList: this.props.bookList.list
    }
    this.menu = (
      <Menu>
        <Menu.Item key="0">
          <a href="#"></a>
        </Menu.Item>
        <Menu.Item key="1">
          <Link to="/setting"><Icon type="setting"/></Link>
        </Menu.Item>
        <Menu.Item key="2">
          <Link to="/about"><Icon type="question-circle-o"/></Link>
        </Menu.Item>
      </Menu>
    )
  }
 
  componentWillMount() {

  }

  componentDidMount() {
    console.log(typeof this.props.bookList);
    // this.props.search('')
    // this.refs.main.setAttribute('class','animated bounceInLeft')
  }

  componentWillReceiveProps(nextProps){
    this.setState({bookList: nextProps.bookList})
  }

  handleRefresh(resolve, reject) {
  // do some async code here
      setTimeout(resolve,2000);
      // console.log(1111);
      // resolve();
  }

  render() {
    return (
      <div className="page" ref="main">
        <Layout>
          <Header className={styles.header}>
            <span className={styles.title}>oho</span>
            <Dropdown
              overlay={this.menu}
              placement="bottomRight"
              trigger={['click']}
              >
              <img src="/static/menu.png" className={styles.dropdown}/>
            </Dropdown>
            <Link to="/search"><Icon type="search" className={styles.search}/></Link>
          </Header>
          
          <Content className={styles.content}>
          <ReactPullToRefresh
            onRefresh={this.handleRefresh}
            icon={(<Spin/>)}
           
          >
            {
              this.state.bookList.map((item, index) => <BookItem data={item} key={index} />)
            }
          </ReactPullToRefresh>
          </Content>
        </Layout>
      </div>
    )
  }
}

AppComponent.defaultProps = {
}

export default template(AppComponent)



