import React from 'react';
import {Layout, Icon, Input, Spin, Button, Tag, message} from 'antd';
import { Link } from 'react-router-dom';
import template from './template';
import styles from '../styles/bookIntroduce.less';
import randomcolor from 'randomcolor';

const { Header, Content } = Layout


class BookIntroduce extends React.Component{
  constructor(props) {
    super(props);
    this.data = {}
    this.state = {
      loading: true,
      save: false
    }
    message.config({
      top: 500,
      duration: 2,
    });
    this.props.getBookIntroduce(this.props.match.params.id);
    this.addBook = () => {
      // this.props.getBookIntroduce(this.props.match.params.id);
      this.props.addBook(this.data);
      message.info('' + this.data.title + '');
       this.setState({save:true});
    }

    this.deleteBook = () => {
      this.props.deleteBook(this.data);
      this.setState({save:false});
      message.info('' + this.data.title + '');
    }
  }

  componentWillMount() {

  }

  componentWillReceiveProps(nextProps) {
    this.data = nextProps.fetchBookItem;
    this.setState({loading: false, save: nextProps.bookList.id.has(nextProps.fetchBookItem._id)});
  }

  handleImageErrored(e){
    e.target.src = '/static/error.jpg';
  }


  render() {
    return (
      <div>
        <Layout >
          <Header className={styles.header}>
            <Link to="/search"><Icon type="arrow-left" className={styles.pre}/></Link>
            <span className={styles.title}></span>
            <span className={styles.share}></span>
            <span className={styles.download}></span>
          </Header>
          <Spin className={styles.loading} spinning={this.state.loading} tip='...'>
          <Content className={styles.content}>
            {
              this.state.loading ? '': 
                (
                  <div>
                    <div className={styles.box}>
                      <img src={this.data.cover} onError={this.handleImageErrored}/>
                      <p>
                        <span className={styles.bookName}>{this.data.title}</span><br/>
                        <span className={styles.bookMsg}><em>{this.data.author}</em> | {this.data.minorCate} | {this.data.wordCount}</span>
                        <span className={styles.updated}>{this.data.updated}</span>
                      </p>
                    </div>
                    <div className={styles.control}>
                      {
                        this.state.save ?
                        (<Button icon='minus' size='large' className={styles.cancel} onClick={this.deleteBook}></Button>) :
                        (<Button icon='plus' size='large' onClick={this.addBook}></Button>)
                      }
                      <Button icon='search' size='large'></Button>
                    </div>
                    <div className={styles.number}>
                      <p><span></span><br/>{this.data.latelyFollower}</p>
                      <p><span></span><br/>{this.data.retentionRatio}%</p>
                      <p><span></span><br/>{this.data.serializeWordCount}</p>
                    </div>
                    <div className={styles.tags}>
                      {
                        this.data.tags.map((item, index) => 
                          <Tag className={styles.tag} color={randomcolor({luminosity: 'dark'})} key={index}>{item}</Tag>
                        )
                      }
                    </div>
                    <div className={styles.introduce}>
                      <pre>{this.data.longIntro}</pre>
                    </div>
                  </div>
                )
            }
          </Content>
          </Spin>
        </Layout>
      </div>
    )
  }
}

export default template(BookIntroduce);


